<template>
  <div id="app">
    <h1 v-text="title"></h1>
    <input type="text" v-model="newitem" v-on:keyup.enter="addNew">
    <button type="submit" @click="addNew">添加</button>
    <ol>
      <!--index用于删除定位-->
      <li v-for="(item,index) in items" v-bind:class="{finish:item.isfinished}" @click="toggleFinish(item)">
        {{item.label}}
        <span><button type="button" @click="delItem">删除</button></span>
      </li>
    </ol>
  </div>
</template>

<script>
  //导入本地存储方法
import Store from './stroe'
export default {
    data: function () {
    return {
      title:'我的todolist',
      items:Store.fetch(),
      newitem:''
    }
  },
  //监听items的变化
  watch: {
    items: {
      handler: function (items) {
        Store.save(items)
      },
      deep: true
    }
  },
  methods: {
    toggleFinish: function (item){
      item.isfinished = !item.isfinished;
    },
    addNew: function(){
      //输入优化，判断是否为空
      if(this.newitem==''){
        alert("请输入非空值！");
        return 0;
      }
      else {
        //传值进items
        this.items.push({
          label: this.newitem,
          isfinished: false
        });
        this.newitem = ''
      }
    },
    delItem: function (index) {
      this.items.splice(index,1)
    }
  }
}
</script>

<style>
#app {
  font-family: '微软雅黑', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
}
input,button{
  border-radius: 5px;
}
button:hover{
  background-color: #ff6dbc;
}
.finish{
    text-decoration:line-through;
  }
</style>



